@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Dancing+Script|Great+Vibes|Parisienne|Tangerine);
.about-title-container{
  padding: 0.75em;
  background: #DCB982;
  font-family: 'Open Sans', sans-serif;
  border-radius: 10px 10px 0 0;
  text-align: center;
}

.about-container {
  margin-top: 5em;
}

.inner-about-container{
  border: 1px solid black;
  border-radius: 10px 10px 0 0;
}

.about-description-container {
  background-color: rgb(152,182,161);
  text-align: center;
  min-height: 5em;
}

.about-description {
  margin: 0;
}

#map-container {
  height: 75vh;
  width: 75%;
  box-shadow: 5px 5px 50px;
  margin: 5em auto;
}

.about-image {
    border-radius: 25px;
    width: 100%;
    box-shadow: 5px 5px 50px;
}

#about-couple-container {
  margin-top: 10em;
}

.inner-about-container{
    box-shadow: 5px 5px 30px;
}

#bottom-row {
  margin-bottom: 5em;
}

body {
  background-color: #EEEEE0;
}

.col-md-3 {
  padding: 0;
}

/*/////Header stuff/////*/
.header {
  margin: 0;
  background-color: rgb(152,182,161);
  padding: 1%;
  width: 100%;
}

#header-title{
  color: #ff4f00;
}

.header a{
  font-family: 'Great Vibes', serif;
  color: black;
  text-decoration: none;
  font-size: 3em;
}

.header * {
  text-align: center;
}

/*///// Carousel fixes /////*/
#scroll-strip {
  height: 0.5em;
  background-color: #ff4f00;
  width: 100%;
  margin: 0px;
}

/*////// Left Nav styles /////*/
.link-item {
  font-family: 'Great Vibes', serif;
  font-size: 3em;
  list-style-type: none;
  text-decoration: none;
  color: black;
  padding-top: 1em;
}
.link-item a {
  color: black;
}

.link-item a:visited{
  color: black;
  text-decoration: none;
}

@media only screen and (max-width: 768px) {
  .header a{
    font-size: 9em !important;
  }
}

